.s-photo-title {
  font-family: "OpenSans-SemiBold";
  font-size: 16px;
  text-transform: uppercase;
}

.s-photo-title h5 {
  margin: 0;
  font-size: 16px;
  padding: 3px;
}

.s-photoListBlock {
  border-top: 1px solid grey;
  border-bottom: 1px solid grey;
  box-shadow: 0 0 5px 2px grey;
  border-radius: 3px;
}

.s-photoListLink {
  height: 40px;
  border: 1px solid #717171;
  border-radius: 5px;
  background-color: rgb(196, 190, 190);
  color: white;
}

.s-photoListLink a {
  text-decoration: none;
  color: white;
  text-align: center;  
}

.s-photoListLink a:hover {
  color: black;
  background: none;
}

.s-video-title {
  font-family: "OpenSans-SemiBold";
  font-size: 16px;
  text-transform: uppercase;
  border-top-left-radius: 5px;
