  border: 1px solid grey;
  padding: 3px;
  border-bottom: none;
}

.s-video-title h5 {
  margin: 0;
  font-size: 16px;
}

.s-videoListBlock {
  border-top: 1px solid grey;
  border-bottom: 1px solid grey;
  box-shadow: 0 0 5px 2px grey inset;
