  border-bottom-right-radius: 5px;
}

.s-videoListLink {
  height: 40px;
  border: 1px solid #717171;
  border-radius: 5px;
  background-color: #999;
  color: white;
}

.s-videoListLink a {
  text-decoration: none;
  color: white;
  text-align: center; 
}

.s-videoListLink a:hover {
  color: black;
  background: none;
}
